import React, {Component} from 'react';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import store from '../store'
export default class NewSongs extends Component {
  constructor(props){
    super(props)
    this.toggle = this.toggle.bind(this)
    this.state= {
      dropdownOpen: false
    }
  }
  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  render() {
    return (
      <div>
        <ButtonDropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>
          <DropdownToggle caret>
            View by Country
          </DropdownToggle>
          <DropdownMenu>
            {/* TODO: get available markets from spotify and iterate */}
            <DropdownItem onClick={this.props.getNewAlbums} value="US">US</DropdownItem>
            <DropdownItem divider />
            <DropdownItem onClick={this.props.getNewAlbums} value="CA">Canada</DropdownItem>
          </DropdownMenu>
        </ButtonDropdown>
      </div>
    );
  }
} 


